



.message {
    display: flex;
    align-items: center;
  }
  
  
  .sent {
    flex-direction: row-reverse;
  }
  
  .sent p {
    color: white;
    background: #0b93f6;
    align-self: flex-end;
    
  }
  .received p {
    background: #e5e5ea;
    color: black;
  }
  .message img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 2px 5px;
  }
  
  