:root{
    /*===== Colores =====*/
    --first-color: #1A73E8;
    --input-color: #80868B;
    --border-color: #DADCE0;
  
    /*===== Fuente y tipografia =====*/
    --body-font: 'Roboto', sans-serif;
    --normal-font-size: 1rem;
    --small-font-size: .75rem;
  }

  .l-form{
      margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 18  0vh;
  }
  .form{
    width: 700px;
    padding: 4rem 2rem;
    border-radius: 1rem;
    box-shadow: 0 10px 25px rgba(92,99,105,.2);
  }
  .form__title{
    font-weight: 400;
    margin-bottom: 3rem;
  }
  
  .form__div{
    position: relative;
    height: 48px;
    margin-bottom: 1.5rem;
    width: 100%;
  }
  .form__div_gender{
    position: relative;
    height: 48px;
    margin-bottom: 1.5rem;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    border-radius: 1rem;
    box-shadow: 0 10px 25px rgba(92,99,105,.2);
  }
  .form__div_confirm{
    position: relative;
    height: 48px;
    margin-bottom: 1.5rem;
    width: 50%;
    display: inline;
    justify-self: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 1rem;
    box-shadow: 0 10px 25px rgba(92,99,105,.2);
    text-align: center;
  }
  .form__div_confirm input{
      margin-top: 10px;
     width: 30px;
     height: 30px;
     margin-right: 10px;
  }
  .form__div_confirm label{
      margin-top: 0px;
      font-size: 20px;
  }
  .form__div_gender_container{
      width: 50%;
      display: inline;
      text-align: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      margin: 12px;
  }
  .form__input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    border: 1px solid #DADCE0;
    border: 1px solid var(--border-color);
    border-radius: .5rem;
    outline: none;
    padding: 1rem;
    background: none;
 
  }
  .form__label{
    position: absolute;
    left: 1rem;
    top: 1rem;
    padding: 0 .25rem;
    background-color: #fff;
    color: #80868B;
    color: var(--input-color);
    font-size: 1rem;
    font-size: var(--normal-font-size);
    transition: .3s;
  }
  .form__button{
    display: block;
    margin-left: auto;
    padding: .75rem 2rem;
    outline: none;
    border: none;
    background-color: #1A73E8;
    background-color: var(--first-color);
    color: #fff;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s;
  }
  .form__button:hover{
    box-shadow: 0 10px 36px rgba(0,0,0,.15);
  }
  .form__input:focus + .form__label{
    top: -.5rem;
    left: .8rem;
    color: #1A73E8;
    color: var(--first-color);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
  }
  .form__input:not(:-ms-input-placeholder).form__input:not(:focus)+ .form__label{
    top: -.5rem;
    left: .8rem;
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
  }
  .form__input:not(:placeholder-shown).form__input:not(:focus)+ .form__label{
    top: -.5rem;
    left: .8rem;
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
  }
  
  /*Input focus*/
  .form__input:focus{
    border: 1.5px solid #1A73E8;
    border: 1.5px solid var(--first-color);
  }  
  /* image */
  .form__div_img{
    width: 700px;
    padding: 4rem 2rem;
    border-radius: 1rem;
    height: 200px;
    
  }
 
